<template>
  <div>
    <h1>Admin Page</h1>
    <v-dialog v-model="dialog" max-width="290">
      <v-card>
        <v-card-title class="headline"> Anda yakin? </v-card-title>

        <v-card-text>
          Pastikan anda konfirmasi kepada pihak sekolah terlebih dahulu!
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn color="error darken-1" text @click="dialog = false">
            Batal
          </v-btn>

          <v-btn color="green darken-1" text @click="confirmUpdate()">
            Yakin
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-simple-table>
      <thead class="grey darken-2 white--text">
        <tr>
          <td>Username</td>
          <td>Email</td>
          <td>Role</td>
          <td>Pelajaran</td>
          <td width="50px">Action</td>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(user, i) in users" :key="i">
          <td>{{ user.username }}</td>
          <td>{{ user.email }}</td>
          <td>
            {{ user.role === 1 ? "Guru" : "Murid" }}
          </td>
          <td>
            <v-expansion-panels
              style="background: transparent"
              hover
              flat
              accordion
              v-if="user.role === 1"
            >
              <v-expansion-panel style="background: transparent">
                <v-expansion-panel-header
                  style="background: transparent"
                  disable-icon-rotate
                  expand-icon="mdi-briefcase-edit-outline"
                >
                  {{ user.mapel || "-" }}
                </v-expansion-panel-header>
                <v-expansion-panel-content style="background: transparent">
                  <v-form ref="myForm" @submit.prevent="updateMapel">
                    <input type="hidden" :value="user._id" />
                    <v-text-field
                      :value="user.mapel"
                      label="Edit Mata pelajaran"
                    >
                      <template v-slot:append>
                        <v-btn type="submit" text color="primary" icon>
                          <v-icon>mdi-send</v-icon>
                        </v-btn>
                      </template>
                    </v-text-field>
                  </v-form>
                </v-expansion-panel-content>
              </v-expansion-panel>
            </v-expansion-panels>
            <div v-else class="ml-6">-</div>
          </td>
          <td>
            <v-btn
              v-if="user.role === 2"
              left
              color="success"
              text
              @click="updateRole(user._id, user.role)"
              >Jadikan guru</v-btn
            >
            <v-btn
              v-if="user.role === 1"
              left
              color="error"
              text
              @click="updateRole(user._id, user.role)"
              >Copot sebagai guru</v-btn
            >
          </td>
        </tr>
      </tbody>
    </v-simple-table>
  </div>
</template>

<script>
import { onMounted, reactive, ref } from "@vue/composition-api";
export default {
  name: "homeAdmin",
  setup(_, { root }) {
    const users = ref([]);
    const dialog = ref(false);
    const myForm = ref(null);
    const form = reactive({
      _id: "",
      role: "",
    });
    const updateRole = (id, role) => {
      dialog.value = true;
      form._id = id;
      form.role = role;
    };
    const confirmUpdate = () => {
      root.axios
        .put("user/alluser", form)
        .then(({ data }) => {
          root.bus.$emit("showSnackbar", {
            message: data.message,
            show: true,
          });
        })
        .catch((err) => console.log(err.response))
        .then(() => {
          getUsers();
          dialog.value = false;
        });
    };
    const updateMapel = (e) => {
      const _id = e.target[0].value;
      const mapel = e.target[1].value;
      root.axios
        .put("user/alluser/mapel", { _id, mapel })
        .then(({ data }) => {
          root.axios
            .post("user/pelajaran/mapel", {
              username: data.user.username,
              name: mapel,
            })
            .catch((err) => console.log(err.response));
          getUsers();
          root.bus.$emit("showSnackbar", {
            message: data.message,
            show: true,
          });
        })
        .catch((err) => console.log(err.response));
    };
    const getUsers = () => {
      root.axios
        .get("user/alluser")
        .then(({ data }) => {
          users.value = data.filter((user) => user.role !== 3);
        })
        .catch((err) => console.log(err.response));
    };
    onMounted(() => {
      getUsers();
    });
    return {
      users,
      updateRole,
      dialog,
      myForm,
      updateMapel,
      confirmUpdate,
    };
  },
};
</script>

